@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
h1 {
  margin: 0;
}

.header-container {
  height: 40vh;
  background-color: rgb(228, 155, 72);
  color: white;
}

.logo {
  padding: 1.5rem;
  max-width: auto;
}

.button-container {
  text-align: center;
}

.button-container h4 {
  margin: 0;
}

.btn-primary {
  color: white;
  display: inline-block;
  background-color: var(--primary-color);
  font-size: 14px;
  text-align: center;
  text-decoration: none;
  border: none;
  padding: 12px 24px;
  margin: 10px;
}

:root {
  --primary-color: rgba(228, 110, 55, 0.75);
  --menu-speed: 0.75s;
}

ul {
  margin: 0;
  padding: 0;
}

.nav-wrap {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1;
}

.nav-wrap .toggler {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
  width: 50px;
  height: 50px;
  opacity: 0;
  margin-right: 20px;
  margin-top: 20px;
  margin-left: 0;
  margin-bottom: 0;
}

.nav-wrap .hamburger {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 21px;
  height: 21px;
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: rgba(228, 110, 55, 0.75);
  background: var(--primary-color);
  margin-right: 20px;
  margin-top: 20px;
}

.nav-wrap .hamburger > div {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  transition: all 0.4s ease;
}

/* Top and bottom lines of hamburger */
.nav-wrap .hamburger > div:before,
.nav-wrap .hamburger > div:after {
  content: "";
  position: absolute;
  z-index: 1;
  top: -10px;
  width: 100%;
  height: 2px;
  background: inherit;
}

/*Moves line down*/
.nav-wrap .hamburger > div:after {
  top: 10px;
}

/* Animation for toggler */
.nav-wrap .toggler:checked + .hamburger > div {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

/* Transform hamburger into an "X" */
.nav-wrap .toggler:checked + .hamburger > div:before,
.nav-wrap .toggler:checked + .hamburger > div:after {
  top: 0;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

/* Rotate when hovering and checked */
.nav-wrap .toggler:checked:hover + .hamburger > div {
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
}

/* Display menu */
.nav-wrap .toggler:checked ~ .menu {
  visibility: visible;
}

.nav-wrap .toggler:checked ~ .menu > div {
  -webkit-transform: scale(1);
          transform: scale(1);
  transition-duration: 0.75s;
  transition-duration: var(--menu-speed);
}

.nav-wrap .toggler:checked ~ .menu > div > div {
  opacity: 1;
  transition: opacity 0.4s ease;
}

.nav-wrap .menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.nav-wrap .menu > div {
  background: rgba(228, 110, 55, 0.75);
  background: var(--primary-color);
  border-radius: 50%;
  width: 150vw;
  height: 150vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: none;
          flex: none;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: all 0.4s ease;
}

.nav-wrap .menu > div > div {
  text-align: center;
  max-width: 90vw;
  max-height: 100vh;
  opacity: 0;
  transition: opacity 0.4s ease;
}

.nav-wrap .menu > div > div > ul > li {
  list-style: none;
  color: white;
  font-size: 1.5rem;
  padding: 1rem;
  margin: 0;
}

.nav-wrap .menu > div > div > ul > li > a {
  color: inherit;
  text-decoration: none;
  transition: color 0.4s ease;
}

.descriptions-container {
  text-align: center;
  height: 133vh;
  width: 75%;
  margin: auto;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.descriptions-container > div {
  margin-top: 200px;
  margin-bottom: 100px;
}

.about-container {
  text-align: center;
